.btn-limpid {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font: 400 18px/1.3 "Rogan-Regular";
  color: #a4b3c5;
  border: 1px solid #a4b3c5;
  background-color: transparent;
  border-radius: 10px;
  padding: 13px 16px;
  margin: 0;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease-out; }
  .btn-limpid:before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 0;
    height: 0;
    border-radius: 10px;
    background-color: #33c474;
    transform: translate(-50%, -50%);
    transition: all 0.2s ease-out;
    z-index: 0; }
  .btn-limpid i {
    display: inline-flex;
    align-items: center;
    position: relative;
    height: 20px;
    font-size: 26px;
    margin-right: 16px;
    z-index: 1; }
  .btn-limpid span {
    display: inline-block;
    position: relative;
    z-index: 1; }
  .btn-limpid:hover {
    color: #ffffff;
    border-color: transparent; }
    .btn-limpid:hover:before {
      width: 110%;
      height: 110%; }
  .btn-limpid:disabled, .btn-limpid.disabled {
    opacity: 0.5;
    pointer-events: none; }

